.App {
  text-align: center;
  background-color: #146464;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.key-logo {
  height: 40vmin;
  pointer-events: none;
  fill: #FFFF00;
}

.craft-logo {
  height: 40vmin;
  pointer-events: none;
  fill: #FF00FF;
}

@media (hover: hover) {
  .selectable-inset {
    transition: background-color 50ms;
  }
  .selectable-inset:hover {
    background-color: #3f555e;
  }
}

.selectable-inset{
  background-color: #2e3e45;
  border-color: #729aab;
  border-width: thick;
}

.ModeButtons {
  display: flex;
  flex-direction: row;
  column-gap: 10px;
  padding: 10px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
